@import "../../sass/variables";

.say-hi {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3rem;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  @include responsive(phone) {
    display: block;
    padding: 3rem 0.8rem;
    height: unset;
  }
}

.say-hi img {
  width: 26rem;
  @include responsive(phone) {
    width: 80%;
  }
}

.say-hi h1 {
  font-size: 8rem;
}
